var Main_index = 0;
var Main_Title=[];//标题数组
var Main_Content=[];//正文数组

export default {
	data() {
		return {
			Main_index:'',
			Main_Title:'',
			Main_Content:'',
			MainPost_list:[
				{Main_index: 0 , Main_Title : '主板块', Main_Content:'这是主板块'}
			]
				 
		}
	},
	
	methods: {
		det(item){
			uni.navigateTo({
				url:'../det/det?id=' + item.Main_index
			})
		},
		Publish(){
			uni.navigateTo({
				url:"../Publish_Editor/Publish_Editor.1"
			})
		},
		handleClick() {
			console.log(this.MainPost_list)
		},
		getData() {
			let Main_Title=[];//标题数组
			let Main_Content=[];//正文数组
			let title;
			let content;
			uni.getStorage({
				key:"PM_ID",
				success: (res) => {
					Main_index = res.data
					for (var i = 1;i <= Main_index ;i++) {
						uni.getStorage({
							key:i + "PM_Title",
							success: (res) => {
								title = res.data;
								Main_Title.push(res.data);
								console.log(Main_Title);
								}
							})
						uni.getStorage({
							key:i + "PM_Content",
							success: (res1) => {
								content = res1.data;
								Main_Content.push(res1.data);
								console.log(Main_Content)
							}
						})
						var Post = {Main_index: i, Main_Title: title , Main_Content: content }
						console.log(132)
						this.MainPost_list.push(Post)
					}
				}
			})
		}
	},
	onLoad() {
		this.getData()
	}
}

